.main{
    min-height: 100vh;
    background-image: url(./images/background1.jpg);
    background-size: cover;
    color:white;
}
.wrapper {
    width: 700px;
    margin: 0 auto;
    padding-top: 3rem;
}
.head-title{
    text-align: center;
    height: 200px;
}
.head-title h1{
    top: 0;
    margin: 0;
    padding: 0;
}
.info-button{
    color:white;
    background:none;
    border: 2px solid #ffb200;
    border-radius:20px;
}
.info-button:hover{
    color: white;
    background-color:#ffb200;
}
.infb1-s{
    width: 140px;
    height: 40px;
}
.infb1-p{
    margin-top:8%;
}
.infb2-s{
    width: 100px;
    height: 30px;
}
.infb2-p{
    margin-left: 20px;
}
.input-add{
    color:white;
    padding: 7px;
    background:none;
    border:2px solid white;
}
.input-add:focus{
    padding:7px;
    border:2px solid #ffb200;
}
.li-items:hover{
    background-color:rgba(255, 255, 255, .1);
    box-shadow: 0 0 5px #4d3703a9,
                0 0 15px #4d3703a9,
                0 0 50px #4d3703a9,
                0 0 200px #4d3703a9
}
.rm {
    width: 50px;
    height: 40px;
    font-size: 30px;
    color: #ffb200;
    border: none;
    border-radius: 10%;
    background: none;
    cursor: pointer;
}

.done {
    text-decoration: line-through;
}
.chkbox{
    font-size: 18px;
}
.chkbox input {
    display: none;
  }
.chkbox span {
    height: 13px;
    width: 13px;
    border: 1px solid gray;
    display: inline-block;
    position: relative;
    background-color:none;
    border-radius:2px;
    padding:3px;
    margin-left:15px;
    margin-right: 15px;
  }
.chkbox [type=checkbox]:checked + span:before {
    content: '\2714';
    position: absolute;
    top: -5px;
    left: 0;
    font-size:23px;
    color:#ffb200;
  }

.lds-dual-ring {
    display: inline-block;
    width: 80px;
    height: 80px;
}

.lds-dual-ring:after {
    content: " ";
    display: block;
    width: 64px;
    height: 64px;
    margin: 8px;
    border-radius: 50%;
    border: 6px solid #fffa;
    border-color: #aaa transparent #aaa transparent;
    animation: lds-dual-ring 1.2s linear infinite;
}

@keyframes lds-dual-ring {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}